@media screen and (min-width: 1064px) {
div.App {
  margin: 5% 5% 5% 0%;
  display: grid;
  grid-template-columns: .7fr 2fr;
  grid-template-rows: 210px 1fr 50px;
}

div {
    color: black;
    text-decoration: none;
}

.links {
    color: black;
    font-size: 20px;
}

.linkDiv {
    margin: 5px 0;
}

.links:hover {
    background-color: rgb(196, 196, 196);
    box-shadow: 0 5px 20px rgba(131, 131, 131, 0.8);
    transform:translateY(-1px);
    padding: 4px;
    border-radius: 5px;
}

nav.linkContainer {
    grid-column: 1/2;
    grid-row: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: right;
    padding-right: 10px;
}

body {
    grid-column: 2/3;
    grid-row: 2/3;
}

.languages {
    grid-row: 2/3;
    grid-column: 1/3;
}

.profileComponent {
    grid-row: 1/2;
    grid-column: 2/3;
}

footer {
    grid-row: 2/3;
    grid-column: 1/2;
    bottom: 0;
}


/*****************
***** FOOTER *****
******************/

.footerComponent {
display: flex;
text-align: center;
overflow: hidden;
justify-content: flex-end;
bottom: 0;
width: 100%;
margin: 30px 0;
}

.githubPhoto {
height: 50px;
width: 50px;
border-radius: 100%;
margin-left: 20px;
}

.linkedInPhoto {
height: 50px;
width: 50px;
border-radius: 100%;
}

/********************
***** Languages *****
*********************/

.languagesComponent {
grid-column: 2/3;
grid-row: 2/3;
display: flex;
flex-direction: column;
}

.languagesContainer {
flex-direction: row;
justify-content: center;
text-align: center;
padding: 5%;
}

.languagesTitle {
font-size: 20px;
}


/* Logo Styling */

.languagesContainer img {
padding: 5px 15px;
}

.languagesContainer .logo {
width: 50px;
}

.languagesContainer .long-logo {
width: 100px;
}

.languagesContainer .postman-logo {
width: 100px;
}

.languagesContainer .mongo-logo {
width: 100px;
}

.languagesContainer .node-logo {
width: 100px;
}

.languagesContainer .heroku-logo {
width: 100px;
}

/*************************
***** PreviousCareer *****
**************************/

body.previousCareerBody {
grid-row: 2/3;
top: 40%;
margin: 0 20px;
background-color: rgb(164, 240, 243);
padding: 5px 10px;
}

.previous-career-first-letter {
font-size: 30px;
margin-left: 15px;
}

/******************
***** Profile *****
*******************/

.profileComponent {
display: grid;
justify-content: right;
grid-template-rows: 1fr 1fr 1fr;
grid-template-columns: 200px 100px;
margin-right: 25%;
animation: 1s ease-out 0s 1 fadeInLeft;
}

.portrait {
height: 200px;
width: 200px;
border-radius: 100%;
grid-column: 2/3;
grid-row: 1/4;
margin-left: -25%;
z-index: 1;
}

.name {
grid-row: 1/2;
grid-column: 1/2;
font-size: 31px;
align-self: flex-end;
margin-left: 10%;
z-index: 0;
}

.pronunciation {
grid-row: 2/3;
grid-column: 1/2;
font-size: 13px;
margin-left: 48%;
z-index: 0;
}

.pronouns {
grid-row: 3/4;
grid-column: 1/2;
font-size: 13px;
margin-top: -25%;
margin-left: 29%;
z-index: 0;
}

@keyframes fadeInLeft {
0% {
    transform: translateX(-10%);
    opacity: 10%;
}
100% {
    transform: translateX(0);
    opacity: 100%;
}
}

/*******************
***** Projects *****
********************/

